import './reset.css';
import './App.scss';
import { useEffect, useRef, useState } from 'react';
import { Canvas } from '@react-three/fiber';
import { OrbitControls, Stars } from '@react-three/drei';
import MoonModeling from './conponents/MoonDraco.js';

function App() {
  // navigator
  const [activeNav, setActiveNav] = useState(0);
  const [showContents, setShowContents] = useState(0);

  //scroll position
  const handleFollow = () => {
    const section0OffsetBotton = document.querySelector('#section0').offsetTop + document.querySelector('#section0').clientHeight;
    const section1OffsetBotton = document.querySelector('#section1').offsetTop + document.querySelector('#section1').clientHeight;
    const section2OffsetBotton = document.querySelector('#section2').offsetTop + document.querySelector('#section2').clientHeight;

    if (window.scrollY < section0OffsetBotton) {
      setActiveNav(0);
      setShowContents(0);
    } else if(window.scrollY < section1OffsetBotton) {
      setActiveNav(1);
      setShowContents(1);
    } else if(window.scrollY < section2OffsetBotton) {
      setActiveNav(2);
      setShowContents(2);
    } else {
      setActiveNav(3);
      setShowContents(3);
    };
  };

  useEffect(()=>{
    window.addEventListener('scroll', handleFollow);
    return () => {
      window.removeEventListener('scroll', handleFollow);
    }
  });

  //scroll to
  function scrollTo(e) {
    const activeSection = document.querySelector("#section" + e);
    const location = activeSection.offsetTop;
    
    if (e == 0) {
      window.scrollTo({top:location, behavior:'smooth'});
    } else if (e == 1) {
      const activeSection = document.querySelector('#section1 > img');
      const location = activeSection.offsetTop;
      window.scrollTo({top:location + activeSection.clientHeight/2 - window.innerHeight/2 , behavior:'smooth'});
    } else if (e == 2) {
      window.scrollTo({top:location + activeSection.clientHeight/2 - window.innerHeight/2 , behavior:'smooth'});
    } else if (e == 3) {
      window.scrollTo({top:location + activeSection.clientHeight/2 - window.innerHeight/2 , behavior:'smooth'});
    }
    
  }

  //three.js
  const mesh = useRef(null);
  // useFrame(() => (mesh.current.rotation.y =  mesh.current.rotation.z += 0.01));
  

  return (
    <div className="App">
      {/* navigator */}
      <nav>
        <ul>
            <li className={activeNav == 0 ? 'active' : null} onClick={() => scrollTo(0)}>
            <a>
              <div className='nav-filter'></div>
              <img src='/assest/img/moon-nav.png'></img>
              <em>Introduction</em>
            </a>
          </li>
          <li className={activeNav == 1 ? 'active' : null} onClick={()=>scrollTo(1)}>
            <a>
              <div className='nav-filter'></div>
              <img src='/assest/img/moon-nav.png'></img>
              <em>Geology</em>
            </a>
          </li>
          <li className={activeNav == 2 ? 'active' : null} onClick={()=>scrollTo(2)}>
            <a>
              <div className='nav-filter'></div>
              <img src='/assest/img/moon-nav.png'></img>
              <em>Moon Card</em>
            </a>
          </li>
          <li className={activeNav == 3 ? 'active' : null} onClick={()=>scrollTo(3)}>
            <a>
              <div className='nav-filter'></div>
              <img src='/assest/img/moon-nav.png'></img>
              <em>3D Moon</em>
            </a>
          </li>
        </ul>
      </nav>
      {/* //navigator */}



      {/* main */}
      <main>
        <section id='wrap'>
          {/* section0 */}
          <div id='section0'>
            <h1>about <em className={showContents == 0 ? 'show' : null}>moon</em></h1>
            <img className={'moon-main ' + (showContents == 0 ? 'show' : null)} src='/assest/img/moon.jpeg' alt="intro moon image"></img>
          </div>
          {/* //section0 */}

          <div className='contents'>
            <div className='container'>
              {/* section1 */}
              <section id='section1' className='content-item'>
                <img src='/assest/img/moon-front.png'></img>
                
                <div className='terrain-wrap'>
                  <div className='terrain-item sea'>
                    <div className={'line ' + (showContents == 1 ? 'show' : null)}>
                      <div className='diagonal'></div>
                      <div className='horizon'></div>
                    </div>
                    <h2 className={showContents == 1 ? 'show' : null}>MARIA <em className={showContents == 1 ? 'show' : null}>dark terrain</em></h2>
                    <p className={showContents == 1 ? 'show' : null}>mare, plural maria, any flat, dark plain of lower elevation on the Moon. The term, which in Latin means sea, was erroneously applied to such features by telescopic observers of the 17th century. In actuality, maria are huge basins containing lava flows marked by craters, ridges, faults, and straight and meandering valleys called rilles and are devoid of water.</p>
                  </div>

                  <div className='terrain-item mountain'>
                    <div className={'line ' + (showContents == 1 ? 'show' : null)}>
                      <div className='diagonal'></div>
                      <div className='horizon'></div>
                    </div>
                    <h2 className={showContents == 1 ? 'show' : null}>HIGH LANDS <em className={showContents == 1 ? 'show' : null}>bright terrain</em></h2>
                    <p className={showContents == 1 ? 'show' : null}>Regions on the moon's surface that are brightly colored except for the sea are called highlands. Continents are densely packed with craters. The minerals that make up the continent appear relatively bright because they are similar to plagioclase plagioclase, which is mainly rich in calcium and aluminum.</p>
                  </div>

                  <div className='terrain-item craters'>
                    <div className={'line ' + (showContents == 1 ? 'show' : null)}>
                      <div className='diagonal'></div>
                      <div className='horizon'></div>
                    </div>
                    <h2 className={showContents == 1 ? 'show' : null}>CRATER <em className={showContents == 1 ? 'show' : null}>recessed terrain</em></h2>
                    <p className={showContents == 1 ? 'show' : null}>impacts. The International Astronomical Union currently recognizes 9,137 craters, of which 1,675 have been dated</p>
                  </div>
                </div>
              </section>
              {/* section1 */}

              {/* section2 */}
              <section id='section2' className='content-item'>
                <div className='moon-card'>
                  <figure className='moon-card-front'>
                    <img className='star' src='/assest/img/star-square.png'></img>
                    <img className='moon-front' src='/assest/img/moon-front.png'></img>
                    <figcaption>Front</figcaption>
                  </figure>
                  <figure className='moon-card-back'>
                    <img className='star' src='/assest/img/star-square.png'></img>
                    <img className='moon-back' src='/assest/img/moon-back.png'></img>
                    <img className='earth' src='/assest/img/earth.png'></img>
                    <figcaption>Back</figcaption>
                  </figure>
                </div>


                {/* <div className='moon-telescope'>
                  <img src='/assest/img/moon-telescope.png'></img>
                </div> */}
              </section>
              {/* section2 */}



              {/* section3 */}
              <section id='section3' className='content-item'>
                <Canvas style={{ width: '40vw', height: '40vw', border: '1px solid #fff', borderRadius: '50%', margin: '0 auto'}}>
                  <OrbitControls/>
                  <MoonModeling/>
                  <Stars/>
                  <spotLight position={[-30, 0, 0]} angle={10}/>
                  <spotLight position={[30, 0, 0]} angle={10}/>
                </Canvas>
              </section>
              {/* section3 */}
            </div>
          </div>
        </section>
      </main>
      {/* //main */}
    </div>
  );
}

export default App;
